:root {
  --color-brand--1: #ffb545;
  --color-brand--2: #00c46a;
  --color-dark--1: #2d3439;
  --color-dark--2: #42484d;
  --color-light--1: #aaa;
  --color-light--2: #ececec;
  --color-light--3: #d6dee0;
  --color-tertiary: #ff585f;
  --color-primary: #5ec576;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

::-webkit-scrollbar {
  width: 10px;
  behavior: smooth;
}

.wrapper {
  width: 100%;
  height: 100%;
}

.sidebar {
  background-color: var(--color-dark--1);
  width: 100%;
  height: 90vh;
  margin: 20px 0;
  overflow-x: hidden;
  overflow-y: scroll;
}

.Logo_img {
  width: 150px;
  height: 70px;
  margin: 50px 0;
}

.workouts {
  list-style: none;
  overflow-x: hidden;
  overflow-y: scroll;
}

.workouts::-webkit-scrollbar {
  width: 0;
}

.form {
  background-color: var(--color-dark--2);
  width: 100%;
  height: 100%;
  border-radius: 10px;
  grid-template-columns: 1fr 1fr;
  gap: .5rem 2.5rem;
  margin-bottom: 1.75rem;
  margin-left: -2%;
  padding: 1.5rem 2.75rem;
  transition: all .5s, transform 1ms;
  display: grid;
}

.form:hover, .form:focus {
  behavior: smooth;
  transition: all 1.25s;
  transform: scale(1.015);
}

.form__row {
  align-items: center;
  display: flex;
}

.form__label {
  flex: 0 0 50%;
  font-size: 16px;
  font-weight: 700;
}

.form__input {
  width: 100%;
  background-color: var(--color-light--3);
  border: none;
  border-radius: 5px;
  margin: 10px 0;
  padding: .4rem 1rem;
  font-family: inherit;
  font-size: .8rem;
  font-weight: bold;
  transition: all .2s;
}

.button__img__form {
  width: 50px;
  height: 50px;
}

.form__input:focus {
  background-color: #fff;
}

.form__messages {
  color: wheat;
  text-align: start;
  text-transform: uppercase;
  font-family: inherit;
  font-size: .7rem;
  font-weight: 600;
}

.workout {
  background-color: var(--color-dark--2);
  width: 100%;
  height: 100%;
  cursor: pointer;
  border-radius: 10px;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 1rem 4rem;
  margin-bottom: 1.75rem;
  margin-left: -2%;
  padding: 1.5rem 2.25rem;
  display: grid;
}

.workout:hover {
  behavior: smooth;
  background-color: #515354;
  transition: all 1.25s;
  transform: scale(1.015);
}

.workout--running {
  border-left: 10px solid var(--color-brand--2);
}

.workout--cycling {
  border-left: 10px solid var(--color-brand--1);
}

.workout--walking {
  border-left: 10px solid var(--color-light--1);
}

.workout--swimming {
  border-left: 10px solid var(--color-tertiary);
}

.workout__title {
  grid-column: 1 / -1;
  font-size: 1.4rem;
  font-weight: 700;
}

.Close__btn {
  width: 30px;
  height: 30px;
  margin: 10px;
}

.workout__details {
  align-items: baseline;
  display: flex;
}

.workout__icon {
  height: .28rem;
  margin-right: .2rem;
  font-size: 1.2rem;
}

.workout__value {
  margin-right: .5rem;
  font-size: 1.1rem;
  font-weight: 600;
}

.workout__unit {
  color: var(--color-light--1);
  text-transform: uppercase;
  font-size: .8rem;
  font-weight: 800;
}

#map {
  width: 100%;
  height: 90vh;
  background-color: var(--color-light--1);
  flex: 1;
  margin: 20px 0;
}

.leaflet-popup .leaflet-popup-content-wrapper {
  background-color: var(--color-dark--1);
  color: var(--color-light--2);
}

.leaflet-popup .leaflet-popup-content {
  text-align: center;
  font-size: 14px;
}

.leaflet-popup .leaflet-popup-tip {
  background-color: var(--color-dark--1);
}

.running-popup .leaflet-popup-content-wrapper {
  border-left: 5px solid var(--color-brand--2);
}

.cycling-popup .leaflet-popup-content-wrapper {
  border-left: 5px solid var(--color-brand--1);
}

.walking-popup .leaflet-popup-content-wrapper {
  border-left: 5px solid var(--color-light--1);
}

.swimming-popup .leaflet-popup-content-wrapper {
  border-left: 5px solid var(--color-tertiary);
}

.question__icon {
  width: 35px;
  height: 35px;
  margin: 10px;
}

.COPYRIGHT__SECTION {
  color: var(--color-light--1);
  margin-bottom: 10px;
  font-size: 15px;
  font-weight: 600;
}

.github_icon {
  width: 30px;
  height: 30px;
  margin: 5px;
}

